.table {
    display: flex;
    flex-wrap: wrap;
    width: 600px;
}
.cell {
    padding: 10px 5px;
    width: 100px;
    max-width: calc(20% - 12px);
    text-align: center;
    border: 1px solid black;
}
.cell:active {
    transition: all .2s ease-in-out;
    transform: scale(1.06);
}